
import './css/Port.css';
import ScrollIndicator from './comp/ScrollIndicator';
import Detail from './comp/Detail';
import {BrowserRouter, Route, Switch} from "react-router-dom";


function App() {
  return (
      <BrowserRouter>
        <Switch>
          <Route exact path="/"><ScrollIndicator basename="/portfolio"/> </Route>
          <Route path="/detail/:num"><Detail /></Route>
        </Switch>
      </BrowserRouter>
  );
}

export default App;
